<template>
    <view class="page-classify">
        <button @click="chooseImage">上传图片</button>
        <button v-if="compressedImageUrl" @click="downloadImage">下载压缩后的图片</button>
    </view>
</template>

<script>
export default {
    data() {
        return {
            compressedImageUrl: '', // 存储压缩后的图片 URL
            apiKey: 'nKz5S8qyXCckvfgD5mjSnhvgGsGdgpdb' // 替换为你的 TinyPNG API 密钥
        };
    },
    methods: {
        // 选择图片
        chooseImage() {
            wx.chooseImage({
                count: 1,
                success: (res) => {
                    this.uploadImage(res.tempFilePaths[0]);
                },
                fail: (err) => {
                    console.error('选择图片失败:', err);
                }
            });
        },
        // 上传图片
        uploadImage(imagePath) {
            wx.uploadFile({
                url: 'https://tinypng.com/backend/opt/store',
                filePath: imagePath,
                name: 'file',
                header: {
                    'Authorization': `Basic ${btoa(this.apiKey + ':')}` // 使用 Base64 编码 API 密钥
                },
                success: (uploadRes) => {
                    const uploadData = JSON.parse(uploadRes.data);
                    console.log('上传成功:', uploadData);
                    this.processImage(uploadData);
                },
                fail: (err) => {
                    console.error('上传失败:', err);
                }
            });
        },
        // 处理压缩后的图片
        processImage(uploadResponse) {
            const { key, originalSize, originalType } = uploadResponse.data;
            wx.request({
                url: 'https://tinypng.com/backend/opt/process',
                method: 'POST',
                data: {
                    key: key,
                    originalSize: originalSize,
                    originalType: originalType
                },
                success: (processRes) => {
                    console.log('处理成功:', processRes.data);
                    this.compressedImageUrl = processRes.data.output.url; // 获取压缩后的图片 URL
                },
                fail: (err) => {
                    console.error('处理失败:', err);
                }
            });
        },
        // 下载压缩后的图片
        downloadImage() {
            if (this.compressedImageUrl) {
                wx.downloadFile({
                    url: this.compressedImageUrl,
                    success: (downloadRes) => {
                        if (downloadRes.statusCode === 200) {
                            wx.saveFile({
                                tempFilePath: downloadRes.tempFilePath,
                                success: (saveRes) => {
                                    console.log('下载成功:', saveRes.savedFilePath);
                                    wx.showToast({
                                        title: '图片已保存',
                                        icon: 'success'
                                    });
                                },
                                fail: (err) => {
                                    console.error('保存失败:', err);
                                }
                            });
                        }
                    },
                    fail: (err) => {
                        console.error('下载失败:', err);
                    }
                });
            } else {
                wx.showToast({
                    title: '没有可下载的图片',
                    icon: 'none'
                });
            }
        }
    }
};
</script>

